/**
 * Backend service configuration.
 *
 * This file centralizes the backend URL configuration for the application.
 * It prioritizes the `VUE_APP_API_BASE_URL` environment variable, which is the standard
 * for Vue CLI projects. This allows for different configurations between environments.
 *
 * If the environment variable is not set, it falls back to a default value
 * suitable for local development.
 */

// Read the backend base URL from Vue CLI's environment variables.
// Vue CLI requires variables to be prefixed with VUE_APP_.
const API_BASE_URL = process.env.VUE_APP_API_BASE_URL || 'http://127.0.0.1:8675';

/**
 * The single, consolidated URL for the backend API.
 * This is used by the Axios instance and for the dev server proxy.
 */
const WEB_SERVICE_URL = API_BASE_URL;

module.exports = {
    WEB_SERVICE_URL
};